var jsUrlHelper = {
  getUrlParam: function(url, ref) {
    var str = "";

    // 
    if (url.indexOf(ref) === -1) return "";

    str = url.substr(url.indexOf("?") + 1);

    var arr = str.split("&");
    for (var i in arr) {
      var paired = arr[i].split("=");

      if (paired[0] === ref) {
        let val = decodeURIComponent(paired[1]); // 
        return val;
      }
    }

    return "";
  },
  putUrlParam: function(url, ref, value) {
    // 
    if (url.indexOf("?") === -1) return url + "?" + ref + "=" + value;

    // 
    if (url.indexOf(ref) === -1) return url + "&" + ref + "=" + value;

    var arr_url = url.split("?");

    var base = arr_url[0];

    var arr_param = arr_url[1].split("&");

    for (var i = 0; i < arr_param.length; i++) {
      var paired = arr_param[i].split("=");

      if (paired[0] === ref) {
        paired[1] = value;
        arr_param[i] = paired.join("=");
        break;
      }
    }

    return base + "?" + arr_param.join("&");
  },
  delUrlParam: function(url, ref) {
    // 
    if (url.indexOf(ref) === -1) return url;
    var arr_url = url.split("?");
    var base = arr_url[0];
    var arr_param = arr_url[1].split("&");
    var index = -1;
    for (var i = 0; i < arr_param.length; i++) {
      var paired = arr_param[i].split("=");
      if (paired[0] === ref) {
        index = i;
        break;
      }
    }
    if (index === -1) {
      return url;
    } else {
      arr_param.splice(index, 1);
      return base + "?" + arr_param.join("&");
    }
  }
};

